@charset "UTF-8";

@import "../scss/variables";
.header {
	padding-top: .75rem;
	padding-bottom: .75rem;
	background: linear-gradient(120deg, #0f75ff 60%, #9d37f6 100%);
	width: 100%;
	z-index: 100;
	box-shadow: rgba(181, 201, 227, 0.12) 0 0 0 1px, rgba(181, 201, 227, 0.24) 0 1px 0 0;
	background: $white;
}

.nav-search {
	padding-right: 15px !important;
	padding-top: 7px !important;
	.form-control.header-search {
		background: $white;
		border: 1px solid #dee2e6;
		border-radius: 3px;
		color: #24273c;
		padding-left: 20px;
		padding-right: 0;
		margin-right: -6px;
		width: 200px;
		border-bottom-right-radius: 0;
		border-top-right-radius: 0;
		border-right: 0;
	}
}

.form-inline {
	.form-control {
		background: $white;
		border: 1px solid #dee2e6 !important;
		border-radius: 3px;
		color: #24273c;
		padding-left: 20px;
		padding-right: 0;
		margin-right: -6px;
		width: 200px;
		border-bottom-right-radius: 0;
		border-top-right-radius: 0;
		border-right: 0;
		&:focus,
		&:hover {
			border: 1px solid #dee2e6 !important;
		}
	}
	.btn {
		border-radius: 0;
		border-color: transparent;
		border-bottom-right-radius: 3px;
		border-top-right-radius: 3px;
	}
	.form-control::-webkit-input-placeholder {
		color: #a7b4c9;
	}
}

.nav-search {
	padding-right: 15px !important;
	padding-top: 7px !important;
}

.navbar-nav .input-icon-addon {
	position: absolute;
	top: 0;
	bottom: 0;
	right: 8px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-pack: center;
	justify-content: center;
	min-width: 2.5rem;
	pointer-events: none;
	color: $white;
}

.form-control::-webkit-input-placeholder {
	color: #a7b4c9;
}

.nav-link.navsearch-icon {
	color: #374254;
}

.navbar .form-inline .btn {
	border-radius: 0;
	padding: 5px 15px 6px 15px;
	border: 2px solid #9d37f6;
	box-shadow: none;
	border-bottom-right-radius: 3px;
	border-top-right-radius: 3px;
}

@media (min-width: 1024px) {
	.form-inline .form-control {
		width: 180px;
	}
}

@media (max-width: 1024px) {
	body {
		&.search-show:before,
		&.sidebar-show:before {
			content: '';
			position: fixed;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			background-color: $black;
			opacity: .6;
			z-index: 891;
		}
		&.search-show {
			.header {
				z-index: 892;
			}
			overflow: hidden;
		}
		&.sidebar-show {
			overflow: hidden;
		}
	}
}

@media (max-width: 991px) {
	body.search-show .form-inline .nav-search {
		display: block;
	}
	body.search-show .header{
		z-index: 891 !important;
	}
	.form-inline .nav-search {
		position: absolute;
		z-index: 892;
		display: none;
	}
	.search-show .nav-search {
		padding: 15px !important;
		left: 0;
		top: 0;
		right: 0;
		width: 100%;
	}
	.form-inline .nav-search {
		display: none;
	}
	body.search-show .form-inline .nav-search {
		display: block;
	}
	.form-inline .nav-search {
		position: absolute;
		z-index: 892;
		display: none;
		.btn {
			margin-top: 0 !important;
			border-radius: 0 3px 3px 0;
		}
		position: absolute;
		right: 24px;
		z-index: 892;
		display: none;
		.form-control {
			float: left;
			width: calc(100% - 41px);
			display: block;
		}
		.btn {
			margin-top: 0;
			border-radius: 0 3px 3px 0;
		}
	}
}

@media (max-width: 414px) {
	.nav-link.navsearch-icon {
		padding-left: 0px;
		margin-top:15px;
	}
	.header .nav-item .badge, .header .nav-link .badge{
		top: -9px;
		right: 2px;
	}
}

@media (min-width: 414px) and (max-width: 1366px) {
	.nav-link.navsearch-icon {
		padding-left: 10px;
		margin-top:15px;
	}
}

.header-search {
	padding: 0;
}

.nav-link.icon i {
	color: #504e70;
}

.app-content .page-header {
	padding: .75rem 1.5rem;
	margin-bottom: 1.5rem;
	border: 1px solid rgb(232, 235, 243);
	background: 0 0;
	min-height: 0;
	background: $white;
	border-radius: 3px;
	.page-title {
		line-height: 0;
	}
}

@media (max-width: 480px) {
	.app-content .page-header {
		padding: 0.75rem 2rem !important;
		margin-bottom: 2rem;
		.page-title {
			margin-bottom: 5px !important;
			line-height: 1.5rem !important;
		}
	}
}

@media (max-width: 567px) {
	.app-content .page-header {
		padding: 2rem 2.1rem 1.2rem 2.1rem;
	}
}

.error-img {
	background: $background;
	.text-dark {
		color: $color !important;
	}
	&:before {
		background: $background;
	}
}

.highlight pre {
	background-color: #ebeff7 !important;
}

.user-tabs ul li {
	&:first-child {
		border-left: 0;
	}
	a {
		padding: 4px 15px;
		font-size: 15px;
		margin: 2px;
		display: block;
		border-radius: 4px;
	}
	border-left: 2px solid rgba(167, 180, 201, 0.3);
}

@media (max-width: 568px) {
	.user-tabs ul li {
		a {
			width: 100%;
		}
		width: 100%;
		display: contents;
		text-align: center;
	}
}

.mail-option .btn {
	box-shadow: none;
}

.timelineleft>li>.timelineleft-item {
	box-shadow: none !important;
	border: 1px solid #e8ebf3;
	border-radius: 4px;
}

.btn-group-xs>.btn,
.btn-xs {
	font-size: .7rem;
	line-height: 17px;
	padding: .02rem .5rem;
}

#count-down-1 {
	margin: 20px 0;
	.clock-presenter {
		height: 80px;
		padding: 0;
		text-align: center;
		.digit {
			margin-top: 20px;
			font-size: 2rem;
			line-height: 40px;
			height: 40px;
			padding: 0 7px;
			display: inline-block;
			overflow: hidden;
			text-align: center;
			position: relative;
			cursor: default;
			background: $white;
			color: $color;
			border-radius: 3px;
		}
		.note {
			position: relative;
			margin-bottom: 0;
			cursor: default;
			font-size: 16px;
			opacity: .7;
		}
	}
}

@media (max-width: 992px) {
	#count-down-1 .clock-presenter .digit {
		font-size: 42px;
	}
	.imag-service {
		width: 100px;
		height: 100px;
	}
}
@media (max-width: 480px){
	.mail-option .hidden-phone {
		display: none;
	}

}

@media (max-width: 767px) {
	#count-down-1 {
		.clock-presenter {
			width: 50%;
			float: left;
			.digit {
				font-size: 36px;
			}
		}
		.hours_dash {
			border-right: none;
		}
	}
	.header-right .responsive-navbar .navbar-collapse .dropdown-menu.dropdown-menu-arrow.dropdown-menu-end {
		left: 10px !important;
		right: 10px !important;
	}
	.dropdown-menu-arrow.dropdown-menu-end:after, .dropdown-menu-arrow.dropdown-menu-end:before{
		display: none;
	}
	.header-right .responsive-navbar .dropdown {
		position: initial;
	}
	.nav-link.leading-none.user-img{
	padding-left: 0px !important;
	}
}

.modal-body .select2-container {
	width: 100% !important;
}

.richText .richText-editor {
	height: 200px !important;
}

@media (min-width: 576px) {
	.modal-dialog {
		width: 800px;
	}
}

@media (max-width: 575.98px) {
	.app-content .side-app {
		padding: 38px 10px 0 10px !important;
	}
}

@media (min-width: 568px) and (max-width: 992px) {
	.app-content .side-app {
		margin-top: 23px;
	}
}

.dash3-counter .counter-icon {
	border: 0;
}

.mail-option {
	.btn-group a.btn,
	.chk-all {
		background: 0 0;
	}
}

.header-brand-img {
	width: 120px;
	height: auto;
	vertical-align: middle;
	margin: 10px auto;
}

@media (max-width: 495px) {
	.nested-media {
		.media-body {
			padding-top: 5px;
			margin-left: 0px;
		}
		.media {
			display: block !important;
		}
	}
	.nav-link.icon.full-screen-link{
	 display:none;
	}
	
}

.media.media-lg img {
	width: 70px;
	height: 70px;
}

.admin-custom-icon {
	width: 60px;
	height: 60px;
	line-height: 60px;
	margin: 0 auto;
	text-align: center;
	background: rgba(0, 0, 0, 0.12);
	border-radius: 50%;
}
.content, .content1 {
    height: 270px;
    max-height: 270px;
    position: relative;
}

.footer .social {
    float: left;
	width: auto;
}
#sales-status,#survey,#purchase,#echart5,#echart1,#lineChart{
	width: 100% !important;
}

